.cases-card {
	$parent: &;

	display: flex;
	flex-direction: column;
	justify-content: space-between;
	width: 100%;
	height: 777px;
	padding: 58px 57px 58px;
	border: 1px solid rgba(255, 255, 255, 0.2);
	border-bottom: none;
	border-right: none;
	&:last-child{
		position: relative;
		&::after{
			content:'';
			position: absolute;
			bottom: -1px;
			right: 0;
			width: 1px;
			height: 55.2%;
			background-color: rgba(255, 255, 255, 0.2);
			transform: translate(80%, 0);
		}
	}
	&:nth-last-child(2) {
		border-bottom: 1px solid rgba(255, 255, 255, 0.2);
	}
	&:last-child{
		border-bottom: 1px solid rgba(255, 255, 255, 0.2);
	}
	@include max(xl) {
		border: none!important;
		border-bottom: 1px solid rgba(255, 255, 255, 0.2)!important;		
		&:first-child{
		border-top: 1px solid rgba(255, 255, 255, 0.2) !important;
		}
		&::after{
			display: none!important;
		}
	}
	@include max(xl) {
		height: auto;
		padding: 58px 30px 58px;
	}

	@include max(lg) {
		padding: 30px 18px;
	}

	&__top {
		display: flex;
		justify-content: space-between;

		@include max(sm) {
			flex-direction: column;
			align-items: center;
			justify-content: flex-start;
		}
	}

	&__title {
		font-size: 48px;
		line-height: 48px;
		letter-spacing: -1.4px;
		color: $white;

		@include max(sm) {
			margin-bottom: 30px;
		}
	}

	&__image {
		margin-top: 20px;
	}

	&__text {
		max-width: 360px;
		font-size: 25px;
		line-height: 30px;
		letter-spacing: -0.5px;
		color: rgba($white, 0.6);
		margin-top: 50px;

		@include max(sm) {
			max-width: 100%;
			font-size: 18px;
			line-height: 20px;
			text-align: center;
		}
	}

	&__author {
		display: flex;
		align-items: center;
	}

	&__author-image {
		width: 77px;
		height: 77px;
		border-radius: 50%;
		overflow: hidden;
		margin-right: 30px;

		img {
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
	}

	&__author-name {
		font-family: $secondFont;
		color: $white;

		p {
			&:first-child {
				font-size: 24px;
				line-height: 27px;
				font-weight: 500;
				letter-spacing: -0.5px;
			}

			&:last-child {
				font-size: 16px;
				line-height: 22px;
			}
		}
	}

	&--green {
		background-color: $green;
	}

	&--blue {
		background-color: $blue-25;
	}

	&--glass {
		background: rgba($white, 0.1);
		backdrop-filter: blur(10px);
	}

	&--small {
		height: auto;
		padding: 45px 30px 42px;
		grid-row: span 2;

		@include max(xl) {
			height: auto;
		}

		@include max(lg) {
			padding: 30px 18px;
		}

		#{$parent} {
			&__title {
				padding-left: 20px;

				@include max(lg) {
					padding-left: 0;
				}
			}

			&__text {
				max-width: 205px;
				font-family: $secondFont;
				font-size: 16px;
				line-height: 22px;
				letter-spacing: 0;
				color: $white;

				@include max(sm) {
					max-width: 100%;
				}
			}
		}
	}

	&--review {
		height: 849px;
		padding: 44px 58px 58px 57px;

		@include max(xl) {
			min-height: 400px;
			height: auto;
			padding: 44px 30px 58px ;
		}

		@include max(lg) {
			padding: 30px 18px;
		}

		#{$parent} {
			&__text {
				max-width: 570px;
				line-height: 32px;
				color: rgba($white, 0.7);
			}
		}
	}
}
